.container {
    display: flex;
    min-height: 100vh;
    flex-direction: column;
}

.main {
    flex: 1;
    text-align: center;
}

.title {
    margin: 2rem 0;
}

.storesList {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, auto));
    justify-content: center;
    width: 80%;
    margin: 0 auto;
}

@media (max-width: 450px) {
    .storesList {
       grid-template-column: 1fr;
    }
  }